html,body{margin:0;background-color: #ff6a51;}
a,ul,li,ol{list-style:none;margin:0;padding:0;text-decoration: none;}
h1,h2,h3,h4,h5,h6,figure,p{margin:0;}
img{vertical-align:top;}

*{box-sizing:border-box;}

header{
  position:fixed;
  top:0;left:0;
  width:20%;
  height:100%;
  text-align:center;
}

.Head-section1{
  padding:30px 0;
  background-color:#fff;
  width:100%;
  height:15%;
}
.Head-section1 p{
  width:100%;
  font-family: SohoGothicPro;
  font-size: 38px;
  font-weight: 300;
}
.Head-section1 p small{
  display:block;
  font-size: 16px;
}
.Head-section2{
  width:100%;
  height:15%;
}
.Head-section2 ul{text-align:right;}
.Head-section2 ul li{
  color:#fff;
  text-align: center;
  padding:30%;
  font-size: 30px;
  background-color:#434a4e;
  border-bottom:1px solid #2f373b;
  font-weight: 300;
  cursor: pointer;
}
.Head-section2 ul li a{
  color:#fff;
}
.Head-section2 ul li:hover{
  background-color:#1ab5b3;
}
/* .Head-bottom{
  width:100%;
  height:70%;
  background-color: #242b2e;
  margin-top: 40px;
} */
.Head-section3{
  text-align:right;
  padding-right:20px;
}
.Head-section3 h2{
  padding:10px 0;
  font-family: Roboto;
  font-size: 19px;
  font-weight: 100;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.68;
  letter-spacing: normal;
  text-align: right;
  color: #1ab5b3;
}
.Head-section3 ul li{
  cursor: pointer;
  padding:1px 0;
  font-family: Roboto;
  font-size: 15px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.85;
  letter-spacing: normal;
  text-align: right;
  color: #6b7072;
}
.Head-section3 ul li:hover{
  color:#fff;
}


.Head-section4{  
  background-color: #242b2e;
  padding:30px 0;
}
.Head-section4 ul{
  display:flex;
  align-items: center;
  justify-content: right;
  margin-right:15px;
  padding-bottom:60px;
}
.Head-section4 ul li{
  margin-right:10px;
}

.Photo-contents{
  width:80%;
  margin-left:20%;
  cursor: pointer;
}
.Photo-contents ul{
  width:100%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(1) > li{
  width:25%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(2) > li:nth-of-type(1){
  width:25%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(2) > li:nth-of-type(2){
  width:50%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(2) > li:nth-of-type(3){
  width:25%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(3) > li:nth-of-type(1){
  width:25%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(3) > li:nth-of-type(2){
  width:50%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(3) > li:nth-of-type(3){
  width:25%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul:nth-of-type(4) > li{
  width:25%;
  display:flex;
  flex-wrap: wrap;
}
.Photo-contents ul li{
  width:auto;
  position:relative;
}
.Photo-contents ul li img{width:100%;}
.Photo-contents ul li .Photo-text{
  position:absolute;
  top:0; left:0;
  width:100%;
  height:100%;
  background-color:seagreen;
  opacity: 0;
  padding:80px 0;
  text-align:center;
  transition:.3s;

}
.Photo-contents ul li .Photo-text:hover{
  opacity: 0.9;
  transition:.3s;
}
.Photo-contents ul li .Photo-text p{
  font-family: Roboto;
  font-size: 20px;
  font-weight: 300;
  color: #fff;
}
.Photo-contents ul li .Photo-text span{
  display:block;
  font-family: Roboto;
  font-size: 14px;
  padding:10px 0 5px 0 ;
  font-weight: 300;
  color: #fff;
}
.Photo-contents ul li .Photo-text span::after{
  content:'';
  display:block;

  width: 57px;
  height: 1px;
  background-color: #fff;
  margin: 20px auto;
}

.Photo-contents ul li .Photo-text img{
  /* width:59px;
  height:59px; */
}

.Quick-menu{
  position:fixed;
  top:0; right:0;
  cursor: pointer;
}
.Quick-contents{
  position:fixed;
  top:90px; right:0;
  width: 300px;
  padding:40px 20px;
  background-color:rgba(0,0,0,0.6);
  display:none;
}
.Quick-contents.on{
  display:block;
}
.Quick-contents ul li{
  display:flex;
  justify-content: flex-start;
  padding:10px 0;
  color:#fff;
  cursor: pointer;
}
.Quick-contents ul li img{width:10%;}
.Quick-contents ul li p{width:80%;padding-left:10px;}

.mbti-content{
  width:70%;
  margin-left:30%;
}
.Photo-footer{
  background-color: #242b2e;
}