.radar{
width:50%;
margin-top: 150px;
position: relative;
margin-left:300px;
}


main{
position: relative;
border:1px solid#ddd;
height: 100vh;
/* width:80%; */
}
.table1{
top:0; right:250px;
position: absolute;
display: flex;
flex-wrap: wrap;
flex-direction: column;
width:100px;
height: 100vh;
/* margin-top: 250px; */
}
.abi{
    background-color: #e8c1a0;
    text-align: center;
    height: 30px;
}

.table1 label{
text-align: center;
height: 30px;
}
.table1 label:nth-of-type(1){
background-color: #ddd;
}
.table1 label:nth-of-type(2){
background-color: #ddd;
}
.table1 label:nth-of-type(3){
background-color: #ddd;
}
.table1 label:nth-of-type(4){
background-color: #ddd;
}
.table1 label:nth-of-type(5){
background-color: #ddd;
}
.table1 .t1{background-color: black;}
.table1 text{font-size: 5rem;}

.table1 td,tr{border:1px solid#000; text-align: center;}
.table1 input:focus {outline: none;}
.table1 input{
text-align: center; 
transform: translateY(-10%);
height: 30px;
}

