html, body{
margin:0; padding:0;
}
.pie{
width:50%;
z-index: 1;
top:0; right:25%;
position: absolute;
transform: translateY(230px);
/* margin:0 auto; */
}
main{
float: right;
width:80%;
position: relative;
height: 100vh;
background-color:rgb(247, 247, 247);
/* z-index: -2; */
/* border:1px solid#000; */
}
.tableP{
position: relative;
z-index: 9999;
float:right;
width:100%;
z-index: 2;
display: flex;
}
.html{
width:20%;
background-color: #e8c1a0;
}
.scss{
width:20%;
background-color: #f47560;
}
.js{
width:20%;
background-color: #f1e15b;
}
.php{
width:20%;
background-color: #e8a838;
}
.react{
width:20%;
background-color: #61cdbb;
}
.btn{
width:20%;
background-color: #222;
cursor: pointer;
}
.btn p{
width:100%;
color:#fff;
text-align: center;
font-size: 2rem;
font-weight: bold;
cursor: pointer;
}
.tableP p{
text-align: center;
font-size: 2rem;
font-weight: bold;
}
.tableP .html p{
background-color: #e8c1a0;
}
.tableP .scss p{
background-color: #f47560;
}
.tableP .js p{
background-color: #f1e15b;
}
.tableP .php p{
background-color: #e8a838;
}
.tableP .react p{
background-color: #61cdbb;
}
.tableP .t1{background-color: black;}
.tableP text{font-size: 5rem;}

.tableP td,tr{border:1px solid#000; text-align: center;}
.tableP input:focus {outline: none;}
.tableP input{
width:100%;
text-align: center; 
height: 30px;
font-size: 20px;
font-weight: bold;
background-color:#ddd;
}